<style lang="less" scoped>
  .steps {
    margin-bottom: 60px;
  }
  .steps-content{
    overflow: hidden;
    margin-bottom: 20px;
  }
  .steps-msg {
    text-align: center;
    font-size: 12px;
    color: #666666;
    margin: 32px 0 28px 0;
    span {
      color: #ffaa00;
    }
  }
  h2 {
    font-size: 14px;
    color: #333;
    background: #F1F3F5;
    padding: 7px 12px;
    margin-bottom: 20px;
  }
  ul{overflow: hidden;}
  .msg-block {
    float: left;
    width: 24%;
    margin-right: 20px;
    margin-bottom: 12px;
    span {
      font-size: 12px;
      display: inline-block;
    }
    .name {
      /*width: 60px;*/
      color: #999;
      margin-right: 8px;
    }
    .content {
      color: #333;
    }
  }
  .node-content {
    overflow: hidden;
    .left {
      float: left;
      width: 49%;
    }
    .right {
      float: right;
      width: 49%;
    }
  }
</style>
<template>
  <div>
    <top-header-back title="申请流程详情"></top-header-back>
    <div class="page-container">
      <div class="steps">
        <h2 class="sub-header">审批进度</h2>
        <div class="steps-msg">{{pageData.created_at | datetime}}
          <span>{{pageData.applicant}}</span>提交申请，
          <span>流程已用时{{pageData.used_time}}</span>。
        </div>
        <el-steps :active="cptStepsNum" align-center space="30%">
          <el-step :title="pageData.applicant" icon="el-icon-circle-check"></el-step>
          <el-step v-for="(item, index) in flow" :key="index" icon="el-icon-circle-check">
            <template slot="title">
              <el-button
                type="text"
                size="small"
                @click="handleDialog(item.id)"
                :disabled="!item.level">
                {{item.level ? $utils.checkLevel(item.level) : item.name}}
              </el-button>
            </template>
          </el-step>
        </el-steps>
      </div>
      <div class="steps-content">
        <h2 class="sub-header">申请内容</h2>
        <ul>
          <li class="msg-block">
            <span class="name">申请内容</span>
            <span class="content">{{pageData.content}}</span>
          </li>
          <li class="msg-block">
            <span class="name">申请角色名称</span>
            <span class="content">{{pageData.content}}</span>
          </li>
          <li class="msg-block">
            <span class="name">流程状态</span>
            <span class="content">{{pageData.content}}</span>
          </li>
          <li class="msg-block">
            <span class="name">申请状态</span>
            <span class="content">{{$utils.checkStatus(pageData.status)}}</span>
          </li>
          <li class="msg-block">
            <span class="name">申请备注</span>
            <span class="content">{{pageData.remark}}</span>
          </li>
          <li class="msg-block">
            <span class="name">审批备注</span>
            <span class="content">{{pageData.rejected_reason || '无'}}</span>
          </li>
        </ul>
      </div>
      <div class="steps-user">
        <h2 class="sub-header">申请角色用户</h2>
        <el-table
          :data="tableData"
          size="mini"
          style="width: 100%">
          <el-table-column
            type="index"
            prop="index"
            label="序号"
            width="60">
          </el-table-column>
          <el-table-column
            prop="name"
            label="用户姓名">
          </el-table-column>
          <el-table-column
            :show-overflow-tooltip="true"
            label="组织路径">
            <template slot-scope="scope">
              {{scope.row.department_route}}
            </template>
          </el-table-column>
          <el-table-column
            prop="position"
            label="工作职务">
          </el-table-column>
          <el-table-column
            label="入职时间"
            width="125">
            <template slot-scope="scope">
              {{scope.row.created_at | datetime}}
            </template>
          </el-table-column>
        </el-table>
      </div>
      <kye-dialog
          width="772px"
          @is-show-dialog="isShowDialog"
          :hasDialog="kyeDialog.hasDialog"
          :title="kyeDialog.title">
          <template slot="body">
            <div class="node-content">
              <div class="left">
                <h2 class="sub-header">审批节点详情</h2>
                <el-table
                  :data="dialogTableData.left"
                  size="mini">
                  <el-table-column
                    label="审批类型">
                    <template slot-scope="scope">
                      {{$utils.checkStep(scope.row.approval_type)}}
                    </template>
                  </el-table-column>
                  <el-table-column
                    label="审批人/角色">
                    <template slot-scope="scope">
                      <div class="table-btn"
                        v-if="$utils.isNotEqual(scope.row.approval_type, 3)"
                        @click="handleApprovalDetail(scope.row.approval_type, scope.row.approval_user_id)">
                        {{scope.row.approval_user_name}}
                      </div>
                      <div class="table-btn"
                        v-else
                        @click="handleApprovalDetail(scope.row.approval_type, scope.row.approval_role_id)">
                        {{scope.row.approval_role_name}}
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column
                    label="流程状态">
                    <template slot-scope="scope">
                      <span :style="{color: $utils.checkStatus(scope.row.status, 'color')}">
                        {{$utils.checkStatus(scope.row.status)}}
                      </span>
                    </template>
                  </el-table-column>
                </el-table>
                <el-pagination
                  background
                  style="text-align: left;"
                  v-show="cptLeftPagination"
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentPageChange"
                  :pager-count="5"
                  :page-size="page.left.size"
                  layout="prev,pager,next"
                  :current-page.sync="page.left.current"
                  :total="page.left.total"
                  prev-text="上一页"
                  next-text='下一页'>
                </el-pagination>
              </div>
              <div class="right">
                <h2 class="sub-header">审批角色详情</h2>
                <el-table
                  :data="dialogTableData.right"
                  size="mini">
                  <el-table-column
                    prop="approval_user_name"
                    label="姓名">
                  </el-table-column>
                  <el-table-column
                    prop="department_name"
                    label="所属部门">
                  </el-table-column>
                </el-table>
                <el-pagination
                  style="text-align: left;"
                  background
                  v-show="cptRightPagination"
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentPageChange"
                  :pager-count="5"
                  :page-size="page.right.size"
                  layout="prev,pager,next"
                  :current-page.sync="page.right.current"
                  :total="page.right.total"
                  prev-text="上一页"
                  next-text='下一页'>
                </el-pagination>
              </div>
            </div>
          </template>
          <template slot="footer">
            <el-button type="primary" size="medium" @click="isShowDialog()">确认</el-button>
          </template>
        </kye-dialog>
    </div>
  </div>
</template>

<script>
import TopHeaderBack from '../../components/header-back'
import KyeDialog from '../../components/dialog'
import api from '../../api'
export default {
  components: {
    TopHeaderBack,
    KyeDialog
  },
  data () {
    return {
      page: {
        left: {
          total: 100,
          current: 1,
          size: 10
        },
        right: {
          total: 100,
          current: 1,
          size: 10
        }
      },
      applicationId: undefined,
      nodeId: undefined,
      pageData: '',
      flow: [],
      tableData: [],
      dialogTableData: {
        left: [],
        right: []
      },
      kyeDialog: {
        hasDialog: false,
        title: '审批节点详情'
      },
      stepsNum: 1
    }
  },
  computed: {
    // 如果总条数大于默认显示数
    cptLeftPagination: function () {
      return this.page.left.total >= 10
    },
    cptRightPagination: function () {
      return this.page.left.total >= 10
    },
    cptStepsNum: function () {
      let arr = this.flow || []
      let active = 1
      for (let i = 0, len = arr.length; i < len - 1; i++) {
        if (arr[i].status !== 3) {
          active = arr[i].level
          break
        } else {
          active = len + 1
        }
      }
      return active
    }
  },
  created () {
    this.applicationId = this.$route.params.id
    api.getApplicationId(this.applicationId).then(res => {
      this.pageData = res.result
      this.tableData = res.result.user
      this.flow = res.result.flow
      // 进度条默认加一条'完成'
      this.flow.push({name: '完成'})
    })
  },
  methods: {
    handleDialog (nodeId) {
      if (!nodeId) return false
      let dialogTableData = this.dialogTableData
      dialogTableData.right = []
      this.nodeId = nodeId
      api.getApplicationNodeUser(this.applicationId, this.nodeId).then(res => {
        let result = res.result
        dialogTableData.left = result.data
        this.page.left.total = result.pagination.total_count
        this.isShowDialog(true)
      })
    },
    handleApprovalDetail (approvalType, userId) {
      if (approvalType !== 3) {
        api.getApplicationNodeUserDateil(this.applicationId, this.nodeId, userId).then(res => {
          let result = res.result
          let dialogTableData = this.dialogTableData
          dialogTableData.right = []
          dialogTableData.right.push(result)
          this.page.right.total = 1
        })
      } else {
        api.getApplicationNodeRoleDateil(this.applicationId, this.nodeId, userId).then(res => {
          let result = res.result
          let dialogTableData = this.dialogTableData
          dialogTableData.right = []
          dialogTableData.right = result.data
          this.page.right.total = result.pagination.total_count
        })
      }
    },
    isShowDialog (bool) {
      if (bool === undefined) {
        this.isShowDialog(false)
        return false
      } else {
        this.kyeDialog.hasDialog = bool
      }
    },
    getSearchData () {
      api.getApplicationList(this.filterCondition).then(res => {
        this.tableData = res.result.data
      })
    },
    // 查询按钮 和 列表条数
    handleSizeChange (val) {
      // 如果val为空表示通过查询按钮触发，使用当前选中的条数
      if (val) {
        this.filterCondition.per_page = val
      }
      this.page.current = this.filterCondition.page = 1
      this.getSearchData()
    },
    // 当前页
    handleCurrentPageChange (val) {
      this.filterCondition.page = val
      this.getSearchData()
    },
    handleTableBtn (id) {
      return api.putApplicationId(id)
    }
  }
}
</script>



